        STORY_1 : `Bizzarro Aperitivo is the newest addition to the Delinquente Wine Co family. Its a take on an all natural, Australian bitter aperitivo, made in the classic Delinquente way with scant regard for tradition, respect for nature and a product all about deliciousness.`,
        STORY_2 : `Bizzarro is made by taking super aromatic organic and biodynamic skin contact Vermentino from the Basham family in the heart of the Riverland and infusing it with traditional aperitivo ingredients such as rhubarb and gentian root providing bitterness. From here Gregs has upped the ante, using organically grown Riverland oranges and lemons to create an intoxicating citrus hit. Finally he fortifies this blend adding neutral grape spirit which has been infused with a secret array of fruits, herbs and other botanicals.`,
