        COMPANY_IMG : 'assets/images/Delinquente/company.jpg',
        STORY_1 : `Con-Greg Grigoriou grew up on a vineyard in the Riverland, South Australia, where his father has managed wineries for over 35 years. A few years ago, while working for an advertising agency in Melbourne, he tasted his first truly interesting Riverland wine and something clicked - why werent people producing high-quality wines in his home region?`,
        STORY_2 : `Gregs idea for Delinquente Wine Co was to take advantage of the Riverlands small vineyards planted with Southern Italian varieties, which are suited to the regions dry Mediterranean climate. He sources grapes from trusted growers many of whom he has know his whole life, who work organically. In the cellar Greg takes this organic philosophy forward, all his wines are small-batch and single-vineyard, fermented in stainless steel with wild yeast and without acid adjustments. His aim is to make wine that is pure, drinkable and delicious - or as he would put it, smashable. To complete his vision he recruited local Adelaide street artist Ankles to design the edgy, eye-catching labels.`,
