        COMPANY_IMG : 'assets/images/Good Intentions Wine Co/company.jpg',
        STORY_1 : `Andrew (farmer) and Louise (Musician) Burchell are a husband and wife team who moved back to Mount Gambier (Andrews childhood town) to start a family and a wine label. They wanted to grip the earth with their toes and use their knowledge of organic farming and permaculture to create their very own wine label. Creating a sense of community was a deep part of this plan, a place to call home and where they could raise their daughter Frankie around home grown food, great music and with a love and appreciation for the land.`,
        STORY_2 : `They lease three vineyards and are planning the planting of two more on their farm, which is located on the outskirts of Mt Gambier, SA. The property and all of the leased vineyards are located on the south side of one of Australias youngest volcanoes. Erupting less than 6000 years ago it is filled with water that changes colour with the seasons. Arising from an ancient inland sea made into limestone and now receiving water from underground caves added with our generous rainfall all contributes to the abundance of water stored in underground limestone caves with pockets of flint rock spread below the nearby soils. The limestone filters the water naturally and the result is a pure water thriving with minerality. All this helps make for incredible soil complexity and their cool climate makes for perfect growing conditions and in turn beautiful, elegant and totally unique wines like no other.`,
        STORY_3 : `They make wines that reflect the eccentricities of their own personalities as well as deeply reflecting place and terroir with a minimalistic approach. They use old world wine methods; preferring the challenges of hand picking fruit, open fermenting with indigenous yeast and only adding sulphur when required by the wine. They do not filter or fine wines and they are all bottled on site in their small winery.
        Mount Gambier is a truly unique wine growing region and one that over the next few years is due likely to grow in understanding and popularity. Over the last few years these wines have gone from strength to strength as cuvees have tightened and become more precise. We are very excited to share these wines with you as we welcome Andrew, Louise & Frankie to our Lo-fi family.`,
