        COMPANY_IMG : 'assets/images/Sonnen/company.jpg',
        STORY_1 : `Sonnen is the side project of talented Tasmanian viticulturist Luke Andree who makes these small batch wines alongside his day job at Mewstone Wines.`,
        STORY_2 : `The journey into wine for Luke was not an obvious one. After moving to Melbourne in 2010 to pursue a physiotherapy degree and chase his dream of playing representative cricket he got a job working at his local Dan Murphys. Bit by bit over his two years at Dans he began to feel more intrigued and comfortable around wine and so after finishing his degree he decided to enrol back into university but this time at Charles Sturt for Viticulture and Winemaking. Whist studying he returned to Tasmania and spent a season pruning in the vines at Stefano Lubaina and a year working the wine bar and cellar door at Moorilla.
        It was by chance at one of the first editions of Bottletops that he met Pat Underwood from Little Reddie and was struck by the world of minimal intervention wine. In late 2016 following this meeting he moved up to Victoria and completed his first vintage at the Boomtown Cooperative winery followed by a full season working at both Oakridge and then Mac Forbes where he spent most of his time helping to manage the vineyards. Post the 2019 harvest with Mac he jetted over to his fathers homeland of Germany to complete vintage with fellow Lo-Fi winery Melshimer. Here he pursued his love of Riesling and worked closely with Thorsten in both the vines and cellar.`,
        STORY_3 : `It was however, the time with Mac that really made him fall in love with the viticultural side of things and what led him back to Tasmania in late 2019 to head up the vineyard and cellar at the very ambitious Mewstone Wines. Here Luke manages almost 6 hectares under vine with the vineyard recently becoming certified organic and he helps make both the Mewstone and Hughes & Hughes wines in their new cellar at the home vineyard in Flowerpot. This is where he makes his Sonnen Wines.
        Sonnen for Luke is the exploration of all he has learnt across his short journey in wine. He wants the wines to speak deeply of Tasmania and the vineyards they are sourced from but have an experimental edge when it comes to winemaking. His release from 2021 wowed us, with his Jannis Riesling a particular highlight, almost reminiscent of the orange wines of Melsheimer. Luke is helping to forge a new path of minimal intervention wines in Tasmania and we cant wait to see Sonnen shine in the years to come.`,
