        STORY_1 : `Christian Tschida cultivates 10 hectares of old vines in the Neusiedlersee region of Burgenland. His vineyards are planted on sandy gravel, schist, and limestone soils and have been in the family for 4 generations. This area of Burgenland has a unique microclimate due to the moderating influence of the vast Lake Neusiedlersee nearby.`,
        STORY_2 : `Christian has a very hands off approach in his vineyards, preferring to let nature take its course. His wild looking parcels are instantly recognisable - overgrown with cover crops that both preserve biodiversity in the vineyards and force the vines to bury their roots deep into the soils in search of nutrients.`,
        STORY_3 : `For Christian, the crucial decision comes at harvest time when he is meticulous about picking the fruit at the optimal point of ripeness - something he judges by taste rather than by measuring the technical sugar levels. In the cellar Christian is both experimental and traditional - constantly trying new vinification techniques but never employing high tech equipment. His wines are all foot stomped, spontaneously fermented in closed tanks that sit outside in the shade, and racked with the help of gravity.
        Christian is spearheading a new wave of great Austrian producers with a non-apologetic, genuine and credible approach.`,
