        STORY_1 : `After moving to Burgundy from Paris in the mid 90, Alexandre Jouveaux officially started producing wine in 2001 alongside his wife Maryse Chatelain, an accomplished winemaker in her own right under the moniker Domaine des Fauvettes. The vineyards from which the two grow their fruit are farmed without chemicals and all harvesting is done by hand. The cellar is located in the town of Uchizy and all of the fruit is pressed in a hand-driven press and no additions are made at any point. The same barrels are used for the same wine each year as they believe that the yeast & bacterias are in symbiosis with the fruit from the vineyards. All wines are bottled without sulphur and total production is minuscule with less than 5,000 bottles in a fruitful year`,
