        STORY_1 : `The Beru family have owned the historical Chateau de Beru domain for 400 years. At the beginning of the 20th century when the phylloxera crisis hit all the vines were uprooted leaving the estate without its source of income. It was only in 1987, that the Comte Eric de Beru, out of passion for wine, undertook to replant the entire vineyard, and in particular, the famous monopole vineyard Clos Beru. His wife, Laurence and their daughter Athenais are now running the domain. Athenais was living in Paris as an insurance broker until 2004 when her father passed away, and she decided to move back to Beru to reclaim the vineyard, which included the painstaking task of converting it to organic, and then biodynamic (now Demeter certified). Her first vintage was 2005 and in the years that have followed she has become one of the most revered and influential winemakers in Chablis.`,
