        COMPANY_IMG : 'assets/images/Domaine Didon/company.jpg',
        STORY_1 : `In 2017 Nama and David Didon took over an old farm in Chassey-le-Camp in Cte Chalonnaise to realise their long held dream of their own biodynamic property. The small village belongs to AOC Bouzeron, which is Burgundys only village appellation for Aligot. David, a seasoned viticulturist spent over a decade consulting with Etienne de Montille in Volnay as well as time in the cellar and fields with friends Julien Altaber and Dominique Derain. The Didons vineyard is a 2ha lieu-dit (historic vineyard) named Vignes Blanches planted to Pinot Noir, Pinot Blanc, Chardonnay and Aligot. It lies on a slope facing east, with a soil of dense yellow limestone and is recognised as one of the very best in the village. The vineyard is worked a few times early in the season by horse and all other work is done by hand limiting the compression of the soil by not using tractors and other heavy machinery.`,
