        COMPANY_IMG : 'assets/images/Domaine Finot/company.jpg',
        STORY_1 : `Thomas Finot, a young winemaker who, after experience in different regions of France, discovered abandoned vineyards in the Coteaux du Gresivaudan (Isre) northeast of Grenoble, and is working to restore the vineyards and the regions potential. With great passion, he is reviving local varieties such as the white Verdesse and the reds Persan. Thomas is committed to organic and biodynamic farming to best express the regions quality. The terroir is an ancient valley with soils of glacial stones and sands with limestone scree and clay and the daytime temperatures are among the highest in France, but with very cool nights. Domaine Finot is also producing a small quantity of superb Crozes-Hermitage from his grandfathers 1.8 hectare of vines on great sites in Larnage on both limestone and granite with white clay (kaolin) soils.`,
