        STORY_1 : `There is new blood in Bugey and he comes by the name Louis Terral. Louis recently settled in the historic stronghold of Mrignat making wines that are truly unique for the region. There is not a sweet nor sparkling wine in sight instead, Louis is making enigmatic examples of still dry Gamay from a selection of steep vineyards in the hills surrounding. He draws upon his years of experience working for the likes of Julie Balagny, Jean Louis Dutraive and Philippe Valette to make wines that truly stand out. Louis is a superstar in the making, doing everything by hand and not taking a single short cut resulting in pure, ethereal examples of mountain Gamay.`,
