        STORY_1 : `Located in the hills of Vallata di Mezzane, near Verona, in Veneto, Silvia has taken over her familys abandoned vineyards and revived them to produce the natural wines as was done in the past without any additives or invasive farming methods. After studying agricultural science this became her passion, tending to the vineyards planted in the 1960s and 1970s to Garganega and Trebbiano di Soave, doing everything from vineyard to cellar in order to know intimately the entire process.`,
